export default function () {
  controller("header", (self) => {
    let btn = self.find(".js-header-btn"),
      menu = self.find(".js-header-menu"),
      body = self.closest("body");

    btn.on("click", function () {
      $(this).toggleClass("active");
      menu.toggleClass("active");
      body.toggleClass("hidden");
      self.toggleClass("open");
    });

    // language
    let lang = self.find(".js-lang"),
      langBtn = self.find(".js-lang-btn"),
      langDrop = self.find(".js-lang-drop");

    langBtn.on("click", function () {
      $(this).toggleClass("active");
      langDrop.toggleClass("show");
    });

    $(window).on("scroll", function () {
      if ($(window).scrollTop() > 1) {
        self.addClass("fixed");
      }

      if ($(window).scrollTop() == 0) {
        self.removeClass("fixed");
      }
    });

    $("body").click(function (event) {
      if (!$(event.target).closest(self).length) {
        btn.removeClass("active");
        menu.removeClass("active");
        body.removeClass("hidden");
        self.removeClass("open");
      }
      if (!$(event.target).closest(lang).length) {
        langBtn.removeClass("active");
        langDrop.removeClass("show");
      }
    });
  });
}
